		font-size: 12px;
		line-height: 16px;
		margin-bottom: 10px;
		overflow: hidden;
		font-family: "Montserrat", Georgia, Cambria, "Times New Roman", sans-serif;
		text-transform: uppercase;
	}
	.carousel-list {
